import { graphql, useStaticQuery } from 'gatsby';
import React, { FC } from 'react';

type Props = {
    title: string
    pathname: string
}
const SEO: FC<Props> = ({ title: subtitle, pathname }) => {
	const { site } = useStaticQuery(graphql`
        query SEO {
            site {
                siteMetadata {
                    title
                    description
                    image
                    siteUrl
                }
            }
        }
    `)
	const { title: mainTitle, description, image: imagePath, siteUrl } = site.siteMetadata;
	const title = `${subtitle? `${subtitle} | ` : ''}${mainTitle}`;
	const url = `${siteUrl}${pathname}`;
	const image = `${siteUrl}/${imagePath}`;

	return (
		<>
			<html lang="en" />
			<title>{title}</title>
			<link rel='canonical' href={url} />

			<meta name="description" content={description} />
			<meta name="image" content={image} />

			<meta name='og:title' content={title} />
			<meta name='og:description' content={description} />
			<meta name='og:image' content={image} />
			<meta name='og:type' content='website' />
			<meta name='og:url' content={url} />
			<meta name='og:site_name' content={mainTitle} />
            
			<meta name="twitter:card" content="summary_large_image" />
			<meta name="twitter:title" content={title} />
			<meta name="twitter:url" content={url} />
			<meta name="twitter:description" content={description} />
			<meta name="twitter:image" content={image} />
			{/* <meta name="twitter:creator" content={seo.twitterUsername} /> */}
		</>
	)
}
export default SEO;
