// eslint-disable-next-line import/named
import { IGatsbyImageData, getImage } from 'gatsby-plugin-image';

export type ImageFile = {
	name: string,
	ext: string,
	relativeDirectory: string,
}

export type Project = {
	name: string,
	headline: string,
	image: {
		icon: string,
		cover: string,
	},
	link: {
		code: string,
		demo: string,
	},
	time: {
		start: string,
		end: string | null,
	},
	tabs: Array<string>,
	roles: Array<string>,
	programming: {
		languages: Array<string>,
		frameworks: Array<string>,
		techniques: Array<string>,
	},
	descriptions: Array<{
		title: string,
		text?: string,
		list?: Array<string>,
		image?: string,
	}>,
	pathname: string,
}

export function parseProject(contents: string, pathname?: string): Project {
	return {
		...JSON.parse(contents),
		pathname: `/${pathname}` ?? '',
	};
}

export function getProjectImageData(pathname: string, fileName: string, files: Array<ImageFile>): IGatsbyImageData {
	return getImage(
		files.find((file) => (
			pathname === `/${file.relativeDirectory}` &&
				fileName === `${file.name}${file.ext}`
		))!
	)!
}
