import { Link, PageProps, graphql, navigate } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import React, { FC } from 'react';
import * as style from './index.module.scss';
import Icon from '../../components/Icon';
import Layout from '../../components/Layout';
import Role from '../../components/Role';
import SEO from '../../components/SEO';
import Tab from '../../components/Tab';
import { ImageFile, Project, getProjectImageData, parseProject } from '../../tools/project';

type SearchParameterNames = 'tabs' | 'roles' | 'languages' | 'frameworks' | 'techniques';
class SearchParameters {
	tabs: Set<string>;
	roles: Set<string>;
	languages: Set<string>;
	frameworks: Set<string>;
	techniques: Set<string>;

	private encode(key: string, value: Set<string>): string {
		return `${key}=${encodeURIComponent([ ...value ].join('&'))}`;
	}
	private decode(parameter: string | null): Set<string> {
		if (parameter === null) return new Set();
		return new Set(
			decodeURIComponent(parameter)
				.split('&')
				.filter(value => (value !== ''))
		);
	}

	constructor(search: string) {
		const urlSearchParameters = new URLSearchParams(search);
		this.tabs = this.decode(urlSearchParameters.get('tabs'));
		this.roles = this.decode(urlSearchParameters.get('roles'));
		this.languages = this.decode(urlSearchParameters.get('languages'));
		this.frameworks = this.decode(urlSearchParameters.get('frameworks'));
		this.techniques = this.decode(urlSearchParameters.get('techniques'));
	}
	toString(): string {
		const parameters = [
			this.encode('tabs', this.tabs),
			this.encode('roles', this.roles),
			this.encode('languages', this.languages),
			this.encode('frameworks', this.frameworks),
			this.encode('techniques', this.techniques),
		]
		return `?${parameters.join('&')}`;
	}
	clone(): SearchParameters {
		return new SearchParameters(this.toString());
	}

	has(key: SearchParameterNames, value: string): boolean {
		return this[key].has(value);
	}
	add(key: SearchParameterNames, value: string): SearchParameters {
		this[key].add(value);
		return this;
	}
	delete(key: SearchParameterNames, value: string): SearchParameters {
		this[key].delete(value);
		return this;
	}
	getNavigationPath(key: SearchParameterNames, value: string): string {
		const newSearchParameters = this.clone();
		return (
			this.has(key, value) ?
				newSearchParameters.delete(key, value).toString() :
				newSearchParameters.add(key, value).toString()
		);
	}

	isEmpty(): boolean {
		return (
			this.tabs.size === 0
                && this.roles.size === 0
                && this.languages.size === 0
                && this.frameworks.size === 0
                && this.techniques.size === 0
		);
	}
	isMatch(project: Project): boolean {
		for (const tab of this.tabs) {
			if (!project.tabs.includes(tab)) return false;
		}
		for (const role of this.roles) {
			if (!project.roles.includes(role)) return false;
		}
		for (const language of this.languages) {
			if (!project.programming.languages.includes(language)) return false;
		}
		for (const framework of this.frameworks) {
			if (!project.programming.frameworks.includes(framework)) return false;
		}
		for (const technique of this.techniques) {
			if (!project.programming.techniques.includes(technique)) return false;
		}
		return true;
	}
}

type DataProps = {
	projects: {
		nodes: Array<{
			fields: {
				contents: string,
			}
			relativeDirectory: string,
		}>
	}
	projectImages: {
		nodes: Array<ImageFile>
	}
}
const ProjectsPage: FC<PageProps<DataProps>> = ({ data, location }) => {
	const tabs = new Set<string>();
	const roles = new Set<string>();
	const languages = new Set<string>();
	const frameworks = new Set<string>();
	const techniques = new Set<string>();
	data.projects.nodes.forEach(node => {
		const project: Project = parseProject(node.fields.contents);
		project.tabs.forEach(tab => tabs.add(tab));
		project.roles.forEach(role => roles.add(role));
		project.programming.languages.forEach(language => languages.add(language));
		project.programming.frameworks.forEach(framework => frameworks.add(framework));
		project.programming.techniques.forEach(technique => techniques.add(technique));
	});

	const searchParameters = new SearchParameters(location.search);

	const sortNames = (name01: string, name02: string): 1 | -1 => {
		return (name01.toLowerCase() < name02.toLowerCase()) ? -1 : 1;
	}

	const projects = (
		data.projects.nodes
			.map(node => parseProject(node.fields.contents, node.relativeDirectory))
			.filter(project => searchParameters.isMatch(project))
			.sort((project01, project02) => sortNames(project01.name, project02.name))
			.sort((project01, project02) => {
				if ((project01.time.end === null) && (project02.time.end === null)) {
					if (project01.name < project02.name) return -1;
				}
				else if (project01.time.end === null) return -1;
				else if (project02.time.end === null) return 1;
				else if (project01.time.end > project02.time.end) return -1;
				return 1;
			})
	);

	return (
		<Layout>
			<div
				className={style.div}
			>
				<header>
					<header>
						<h1>FILTER</h1>
						<h2>{projects.length} results</h2>
						<button
							disabled={searchParameters.isEmpty()}
							onClick={() => navigate('#')}
						>
                            clear all
						</button>
					</header>
					<section>
						<h2>Tabs</h2>
						<nav>
							{[ ...tabs ]
								.sort(sortNames)
								.map(tab =>
									<Tab
										key={`tab_${tab}`}
										name={tab}
										isFocus={searchParameters.has('tabs', tab)}
										navigateTo={searchParameters.getNavigationPath('tabs', tab)}
									/>
								)
							}
						</nav>
					</section>
					<section>
						<h2>Roles</h2>
						<nav>
							{[ ...roles ]
								.sort(sortNames)
								.map(role =>
									<Role
										key={`role_${role}`}
										name={role}
										isFocus={searchParameters.has('roles', role)}
										navigateTo={searchParameters.getNavigationPath('roles', role)}
									/>
								)
							}
						</nav>
					</section>
					<section>
						<h2>Techniques</h2>
						<nav>
							{[ ...techniques ]
								.sort(sortNames)
								.map(technique =>
									<Link
										key={`technique_${technique}`}
										to={searchParameters.getNavigationPath('techniques', technique)}
									>
										<Icon
											name={technique}
											isFocus={searchParameters.has('techniques', technique)}
										/>
									</Link>
								)
							}
						</nav>
					</section>
					<section>
						<h2>Frameworks</h2>
						<nav>
							{[ ...frameworks ]
								.sort(sortNames)
								.map(framework =>
									<Link
										key={`framework_${framework}`}
										to={searchParameters.getNavigationPath('frameworks', framework)}
									>
										<Icon
											name={framework}
											isFocus={searchParameters.has('frameworks', framework)}
										/>
									</Link>
								)
							}
						</nav>
					</section>
					<section>
						<h2>Languages</h2>
						<nav>
							{[ ...languages ]
								.sort(sortNames)
								.map(language =>
									<Link
										key={`language_${language}`}
										to={searchParameters.getNavigationPath('languages', language)}
									>
										<Icon
											name={language}
											isFocus={searchParameters.has('languages', language)}
										/>
									</Link>
								)
							}
						</nav>
					</section>
				</header>
				{projects.map(project =>
					<Link
						key={`project_${project.pathname}`}
						to={project.pathname}
						className={style.project}
					>
						{project.image.icon?
							<GatsbyImage
								className={style.icon}
								image={getProjectImageData(
									project.pathname,
									project.image.icon,
									data.projectImages.nodes
								)}
								alt={`${project.name} icon`}
								loading='eager'
							/>:
							<div className={style.icon} />
						}
						<span
							className={style.name}
						>
							{project.name}
						</span>
						<span
							className={style.headline}
						>
							{project.headline}
						</span>
					</Link>
				)}
			</div>
		</Layout>
	)
}
export default ProjectsPage;

export const Head: FC<PageProps> = ({ location }) => (
	<SEO
		title='Projects'
		pathname={location.pathname}
	/>
)

export const query = graphql`
    query Project {
		projects: allFile(
			filter:{
				name: {eq: "project"},
				extension: {eq: "json"},
                relativeDirectory: {regex: "/^projects//"}
			}
		) {
			nodes {
				fields {
					contents
				}
				relativeDirectory
			}
		}
		projectImages: allFile(
			filter: {
				extension: {regex: "/png|jpg/"},
				relativeDirectory: {regex: "/^projects//"}
			}
		) {
			nodes {
				childImageSharp {
					gatsbyImageData
				}
				name
				ext
				relativeDirectory
			}
		}
	}
`
