import { Link } from 'gatsby';
import React, { FC } from 'react';
import * as style from './index.module.scss';

type Props = {
    name: string,
    isFocus?: boolean,
	navigateTo?: string,
}
const Role: FC<Props> = ({ name, isFocus=false, navigateTo }) => {
	return (
		<Link
			to={navigateTo ?? `/projects/?roles=${encodeURIComponent(name)}`}
			className={`${style.link} ${isFocus ? style.focused : ''}`}
		>
			#{name}
		</Link>
	)
}
export default Role;
